import * as tf from '@tensorflow/tfjs';


class Simulation {
  constructor() {
    this.cars = []
    this.x_scale = [0.00840336, 0.00840336, 0.00840336, 0.00840336, 0.00840336,
      0.00840336, 0.00840336, 0.00840336, 0.00840336, 0.00840336,
      0.00840336, 0.00840336, 0.00840336, 0.00840336, 0.00840336,
      0.00840336, 0.00840336, 0.00840336, 0.00840336, 0.00840336,
      0.00840336
    ]
    this.y_scale = 3251.8414074
    this.y_mean = 48924.98394408
    this.on_load = null
    // this.model = tf.loadModel('https://foo.bar/tfjs_artifacts/model.json');
  }

  async load() {
    this.model = await tf.loadModel('./model/model.json') //tf.io.browserFiles(['./model/model.json','./model/group1-shard1of2','./model/group1-shard2of2']));
    if(this.on_load!=undefined) this.on_load(this)
  }



  predict(pop) {
    var popk = pop.slice(0)
    for (var i = 0; i < popk.length; i++) {
      popk[i] = pop[i]*this.x_scale[i]
    }

    var arrp = []
    arrp[0] = popk
    var tensor = tf.tensor(arrp)
    var p = this.model.predict(tensor)

    return p.dataSync()[0]*this.y_scale+this.y_mean
  }
}

var sim = new Simulation()
sim.load()
window.Simulation = sim

export default sim