import L from 'leaflet'
import {
  sign_groups
} from './signs';

function interpolatePosition(p1, p2, duration, t) {
  var k = t / duration;
  k = (k > 0) ? k : 0;
  k = (k > 1) ? 1 : k;
  return L.latLng(p1.lat + k * (p2.lat - p1.lat),
    p1.lng + k * (p2.lng - p1.lng));
}



class Car {
  constructor(id, waiting, moves) {
    this.id = id
    this.waiting = waiting
    this.moves = moves
  }
}



class Move {
  constructor(from, to, color) {
    this.from =  L.latLng(from)
    this.to = L.latLng(to)
    this.color = color
  }

  vector_size() {
    var a0 = this.to.lng - this.from.lng
    var a1 = this.to.lat - this.from.lat
    return Math.sqrt(a0 * a0 + a1 * a1)
  }
}

