    <el-dialog id="traffic_info" class="traffic_dialog" title="Info" @close="traffic_visible = ''" :visible="traffic_visible == 'info' " width="650px" center>
      <span>

        <h2>Goal</h2>
        <hr/>
        <p>The goal of the project is to visualize our traffic signal control algorithms.</p>
        <br/>
        <h2>Representation</h2><hr/>

        <p>The
          <strong>map</strong> presents traffic signal settings for 21 crossroads at
          <strong>Stara Ochota district in Warsaw</strong>.
          <br/><br/> Each number next to a traffic signal is an offset - number from the set {0,1,2,...,119} representing the number of seconds from the beginning of the simulation to the first switch from a
          <strong>red</strong> signal phase to a
          <strong>green</strong> signal phase for a given signal. For each crossroad, we visualize offsets only for 1 traffic signal being a representative of the whole crossroad (the rest is synchronized to ensure traffic safety), to see the complete configuration of red and green signal states, one should click on a given traffic signal - then, it is possible to modify offsets.</p>
        <br/>
        <h2>Current Settings</h2><hr/>

        <p>In the current setting, we assume that durations of red signal phases are always 62 seconds, while durations of green signal phases are always 58 seconds. </p><br/>

        <p>For each setting, we compute
          <strong>"Traffic score"</strong>: the total time of waiting on red signals during
          <strong>10 minutes of traffic</strong> (we can simulate it, but we can also use machine learning algorithms, trained on data from simulations to speed up evaluations). </p>

        <p>We can also run an optimization algorithm (based on a genetic algorithm) to find a good setting (we can run it using the
          <strong>"Find best setting"</strong> button).</p>

<br/>
<h2>Change Values</h2><hr/>

<p>Possibility to change a value of the offset for the traffic signal at crossroad 2 (highlighted on the left side of the map).</p>
<br/>
<img src="./images/info01.png" alt="">
<br/>
<p>
It is also possible to click Find best setting which opens a windows allowing to set values of parameters of a simple version of our genetic algorithm, configured to find (sub)optimal settings of traffic signal offsets.

</p>
<br/>
<img src="./images/info02.png" alt="">
<br/>
<p>
  When values are properly set and we click Run Genetic Algorithm, we can observe a run of the genetic algorithm instance and see on a chart how it finds better and better settings of traffic signals, which reduces values of the fitness function (the total time of waiting on a red signal) over time (Figure 7). It is possible to see the best value found so far in the top-right corner of the chart. The the best value found by a given run of the genetic algorithm is compared with the best value found so far (in all previous runs of a user) and the best value (and best signal offset settings) can be replaced.

</p>


<br/>
<strong>
<img src="./images/info03.png" alt="">
Figure 7:
</strong>
 <i>Chart presenting evolution of the genetic algorithm and values of the fitness function (the total time of waiting on a red signal) for best settings founds in successive iterations.</i> 
<br/><br/>


<p>
  Even if the genetic algorithm run doesnt find better setting, it is always possible to Load Best Solution found so far by clicking the corresponding button in the Traffic score panel.
<br>
When we want to investigate the given traffic signal setting with more details, we can click the Simulation button in the top-right corner of the map,which opens additional window giving opportunity to run a traffic simulation (Figure 8).
</p>

<br/>
<strong>
<img src="./images/info04.png" alt="">
Figure 8:
</strong>
<i>Window with options to run new traffic simulation (or import the simulation generated previously).</i>
<br/><br/>
<p>
  When we click "Run Simulation the app sends a new request to the microservice with a traffic simulation, which computes positions and speeds of cars in every seconds, for a given setting of traffic signals. Then, after receiving response from microservice, the app starts visualizing simulation (Figure 9) and shows a chart presenting the average speed (Figure 10).
</p>

<br/>
<strong>
<img src="./images/info05.png" alt="">
Figure 9:
</strong>
Window with options to run new traffic simulation (or import the simulation generated previously).
<br/>

<br/>
<strong>
<img src="./images/info06.png" alt="">
Figure 10:
</strong>
<i> The average speed of all cars during the simulation.</i>
<br/><br/>

<p>
After the end of the simulation (600 steps = 600 seconds of traffic), we can export the simulation the an external file, in order to import in later, or see Traffic Statistics (Figure 10). Among statistics computed during the simulation, we have the average number of cars in a proximity of a given traffic signal (per second), average speed of that cars and the average number of cars waiting on a red signal at the end of a given road segment. The information is displayed for all 21 crossroads (the Name is just a name of the street leading to the traffic signal being a representative of a given crossroad, Position contains its geographical coordinates (in WGS84).
</p>

<br/>
<strong>
<img src="./images/info06.png" alt="">
Figure 11:
</strong>
<i>Table presenting statistics of the simulation.</i>
<br/><br/>

<p>
  When we click Run simulation the app sends a new request to the microservice with a traffic simulation, which computes positions and speeds of cars in every seconds, for a given setting of traffic signals. Then, after receiving response from microservice, the app starts visualizing simulation (Figure 9) and shows a chart presenting the average speed (Figure 10).
</p>

