import genetic from '../js/genetic.js'
import MessageBox from './MessageBox'
import NumberInput from './number-input'
import { sign_groups } from '../js/signs.js'

import {dragElement} from '../js/helpers.js'

const https = require("https");
const url_compute =
  "http://13.81.247.156:25041/compute";
  const url_results =
  "http://13.81.247.156:25041/results";

var ourCustomControl = L.Control.extend({
  options: {
    position: 'topleft' 
    //control position - allowed: 'topleft', 'topright', 'bottomleft', 'bottomright'
  },
});

function deg2rad(deg) {
  return deg * (Math.PI/180)
}

function getDistanceFromLatLonInKm(lat1,lon1,lat2,lon2) {
  var R = 6371; // Radius of the earth in km
  var dLat = deg2rad(lat2-lat1);  // deg2rad below
  var dLon = deg2rad(lon2-lon1); 
  var a = 
    Math.sin(dLat/2) * Math.sin(dLat/2) +
    Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) * 
    Math.sin(dLon/2) * Math.sin(dLon/2)
    ; 
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
  var d = R * c; // Distance in km
  return d;
}



function get_datetime_str() {
  var currentdate = new Date(); 
var datetime =  currentdate.getFullYear() + 
      "-" + (currentdate.getMonth()+1) + 
      "-" + currentdate.getDate() + " "
              + currentdate.getHours() + ":"  
              + currentdate.getMinutes() + ":" 
              + currentdate.getSeconds();
 return datetime;
}

const axios = require('axios');

function gtime1(time) { //green
      return  (time == 0) ? 0
      : time == 1 ? 0
      : time == 120 ? 0
      : time<=60 ? time-2
      : 0 // time-62;
    }

function gtime2(time) { //red
    return  (time == 0 )  ? 60
      :  time == 1 ? 61
      : time == 120 ? 60
      :  time<=60 ? 62
      :  time % 60
  }

  function gtime3(time) { //green
    return (time == 0 ) ? 58
      :  time == 1 ? 58
      : time == 120 ? 58
      : time<=60 ? 60-time
      : 58;
    }

  function gtime4(time) { //red
    return (time == 0 ) ? 2
      : time == 1 ? 1
      : time == 120 ? 2
      : time<=60 ? 0
      : 122-time;
    }



   function rtime1(time) { //red
      return  (time == 0 )  ? 0
      : time==120 ? 0
      : time == 1 ? 1
      : time<=62 ? time
      : 0;
    }

function rtime2(time) { //green
    return  (time == 0 )  ? 58
       : time==120 ? 58
      : time == 1 ? 58
      : time<=62 ? 58
      : time - 62
  }

  function rtime3(time) { //red
  return (time== 0 )  ? 62
     : time==120 ? 62
    :  time == 1 ? 61
    : time<=62 ? 62-time
    : 62;
  }

   function rtime4(time) { //green
   return (time== 0 )  ? 0
     : time==120 ? 0
    :  time == 1 ? 0
    : time<=62 ? 0
    : 120-time;
    }

const delay = ms => {
  return new Promise(resolve => {
    setTimeout(resolve, ms)
  })
}

const send_http = function(url,body)  {
  return new Promise(resolve => {
     var xmlhttp = new XMLHttpRequest();   // new HttpRequest instance 
     xmlhttp.timeout = 100000;
        xmlhttp.onreadystatechange = function() {
          if(this.readyState==4) {
             if ((this.status == 200 || this.status == 201)) {
                resolve(this.response);
            } else {
              // throw new TypeError('Throwing');
              resolve(JSON.stringify( {
                isError: true,
                status: this.status,
                statusText: this.statusText
              } ));
            }
          }
        };  
        xmlhttp.open("POST", url);
        xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xmlhttp.send(body);
  })
}
     
