    traffic_get_time(i, sign) {
      i = i % 120;
      if(i<sign.time1) {
        return [sign.time1-i, sign.isGreen ];
      }
      if(i<sign.time2) {
        return [sign.time2-i, !sign.isGreen ];
      }
      if(i<sign.time3) {
        return [sign.time3-i, sign.isGreen ];
      }
      

      return [ sign.time4  -i, !sign.isGreen ];
    },

    traffic_set_init() {
      for(let m of this.mapMarkers) {
        let t = m.sign.group.time;
        if(m.sign.isGreen) {
          m.sign.time1 = gtime1(t);
          m.sign.time2 = gtime2(t) + m.sign.time1;
          m.sign.time3 = gtime3(t) + m.sign.time2;
          if(m.sign.time3>=120 && m.sign.time1>0) {
            m.sign.time3 += m.sign.time1;
          }
          m.sign.time4 = 58 + m.sign.time3;
        } else {
          m.sign.time1 = rtime1(t);
          m.sign.time2 = rtime2(t) + m.sign.time1;
          m.sign.time3 = rtime3(t) + m.sign.time2;
          if(m.sign.time3>=120 && m.sign.time1>0) {
            m.sign.time3 += m.sign.time1;
          }
          m.sign.time4 = 62 + m.sign.time3;
        }
      }
    },
    
    traffic_set(i) {
      if(i<0) { //turn off traffics
        this.traffic_clear_signs();
      } else {
        for(let m of this.mapMarkers) {
          // #var time = 0;
          // var isGreen = false;
          let res = this.traffic_get_time(i,m.sign);
          m.unbindTooltip();
          m.bindTooltip( 
                String(res[0]), 
                {
                permanent: true, 
                className: res[1] ? "my-green" : "my-red",
                opacity: 0.9,
                offset: [10, 5] 
                });  
        }
      }
    },

    


    async simulation_statistics_open() {
      this.simulation_statistics_table = [];
      this.isLoading_screenText = "Please Wait. Calculating Statistics..."
      this.isLoading_screen = true;
      
      await delay(200);

      try {
        for(var key in this.sign_groups) {
          let group = this.sign_groups[key];
          group.cars = 0;
          group.waiting_cars = 0;
          group.total_speed = 0;

          group.cars2 = 0;
          group.waiting_cars2 = 0;
          group.total_speed2 = 0;
          group.time2 = 0;

          if(this.simulation2_score>0) {
            group.time2 = this.simulation2_signals[group.id];
          }
      }

      let traffics = [];
      

      for(let i=0;i<600;i++) {
        // let r = ; //each simulation statistics
        for(let car of this.simulation[i]) {
          let min_dist = 1000000000.000;
          let min_sign = undefined;
          for(var key in this.sign_groups) {
              let group = this.sign_groups[key];
              
              let distance = getDistanceFromLatLonInKm(
                group.sign_marker.position[0],
                group.sign_marker.position[1],
                car[2],car[3])

              if(distance<min_dist) {
                min_sign = group;
                min_dist = distance;
              }
          }
          min_sign.cars += 1;
          min_sign.total_speed += car[1];
          if(car[1]<=0) {
            min_sign.waiting_cars += 1;
          }
        }
      }

        if(this.simulation2_score>0) {
          for(let i=0;i<600;i++) {
                // let r = ; //each simulation statistics
                for(let car of this.simulation2[i]) {
                  let min_dist = 1000000000.000;
                  let min_sign = undefined;
                  for(var key in this.sign_groups) {
                      let group = this.sign_groups[key];
                      
                      let distance = getDistanceFromLatLonInKm(
                        group.sign_marker.position[0],
                        group.sign_marker.position[1],
                        car[2],car[3])

                      if(distance<min_dist) {
                        min_sign = group;
                        min_dist = distance;
                      }
                  }
                  min_sign.cars2 += 1;
                  min_sign.total_speed2 += car[1];
                  if(car[1]<=0) {
                    min_sign.waiting_cars2 += 1;
                  }
                }
              }
        }

      } finally {
        this.isLoading_screen = false;
      }

    },

    simulation_open() {
      this.isSimulation_open = true;
      this.isSimulation_finish = true;
      this.isSimulation_text = 'Click run to get all simulations';
      this.traffic_set_init();
      let v=  this;
      Vue.nextTick(function () {
         v.init_chartsim();
         v.traffic_set(0);
      })

     
    },

   async simulation_loop_status(xmlhttp) {
     while(this.isSumulation_downloading) {
      if(this.isSumulation_finish)  return;
      this.isSimulation_text = "Waiting for calculations: " +
        Math.round((Date.now()-this.isSumulation_loading_time)/1000) + "s";
      
       await delay(500);
     }
