   simulation_init() {
        this.simulation = [];
        if(this.simulation_markers!=undefined)
        {
          this.simulation_markers.clear_cars();
        }
        this.simulation_markers = L.Marker.movingMarker(this.map, this.simulation, this.car_on, this.car_off);
    },

    get_avg_speed(res) { 
      let sum = 0;
      let length = res.length;
      for(let r of res) {
         sum+= r[1];
      }
      return  Math.round( sum/length * 100) / 100;
    },

   async simulation_download_times() {
      let v = this;
      v.simulation = [];
      v.isSimulation_text = "Download simulations: ";
      v.simulation_time = 0;
      v.simulation_init();
      v.chartsim_set(this.simulation_score);

      this.$notify({
          title: 'Success',
          message: 'Simulation calculated, start downloading',
          type: 'success'
        });

      var folder_text = "output";
      for(let s of this.sign_times) {
        folder_text += "_" + String(s);
      }
      
      for(let i=0;i<600;i++) {

        try {
            this.isSimulation_text = "Download simulations: " + (i+1) + "/600";
            var time = '{"time":"' + String(i) +'","outdir": "'  + folder_text + '"  }';
            let res = JSON.parse(await send_http(url_results,time));
            if(res.isError!=undefined) {
               if(!v.isSumulation_loading) return;
                v.$notify({
                    title: 'Error while downloading / Software will try repeat after 5 seconds. Please click "cancel" to stop.',
                    message: res.statusText,
                    type: 'error'
                  });
                  await delay(5000);
                  i = i-1;
             
              continue; 
            }
            res = convert_text( res.cars);
          
            v.simulation_markers.add_values(res);
            let avg_speed = this.get_avg_speed(res);
            this.chartsim_add(i,avg_speed);

            v.simulation.push(res);
            v.simulation_time = i;
            v.simulation_markers.car_set(i);
            this.traffic_set(i);

            if(!v.isSumulation_loading) return;
            await delay(100);

        } catch(error) {
          this.$notify({
            title: 'Errors during download',
            message:  error,
            type: 'danger'
          });
          // console.error('Errors during download', error);
        }
        

      }


      v.stop_download("Simulation run", 0);
   },

   stop_download(text, time, type="success") {
       // isSumulation_loading = false"
      this.isSimulation_text = text;
      this.isSumulation_finish = true;
      this.isSumulation_loading = false;
      this.simulation_time = time;
      if(this.simulation_markers!=undefined)
      {
          this.simulation_markers.car_set(time);
      }
      this.traffic_set(time);

      this.$notify({
          title: type,
          message: text,
          type: type
        });
   },
   
   simulation_export() {

      let csvContent = JSON.stringify({
         simulation_score: this.simulation_score,
         sign_times: this.traffic_get_population(),
         simulation_times:  this.simulation
      });
      
      var blob = new Blob([csvContent], {type: 'text/json'});
      var filename = get_datetime_str() + "-" + String(this.simulation_score) + '-simulation.json'; // $scope.getFileNameFromHttpResponse(result);
      if(window.navigator.msSaveOrOpenBlob) {
          window.navigator.msSaveBlob(blob, filename);
      }
      else{
          var elem = window.document.createElement('a');
          elem.href = window.URL.createObjectURL(blob);
          elem.download = filename;
          document.body.appendChild(elem);
          elem.click();
          document.body.removeChild(elem);
      }
      return
   },

   async simulation_import_compare() {
     let v = this;
     var element = document.createElement('div');
     element.innerHTML = '<input type="file" accept=".json">';
     var fileInput = element.firstChild;

     fileInput.addEventListener('change', function() {
          var file = fileInput.files[0];

          if (file.name.match(/\.(xml|txt|json)$/)) {
              var reader = new FileReader();

              reader.onload = async function() {
                  var r = JSON.parse(reader.result);
                  
                  v.simulation2 = [];
                  v.simulation2_score = r.simulation_score;
                  v.simulation2_signals = r.sign_times;

                  v.isLoading_screenText = "Please Wait. Importing 2nd simulation" ;
                  v.isLoading_screen = true;
                  
                  // await delay(200);
                  // v.isSimulation_text = "Please Wait. Importing 2nd simulation" ;


                  v.chartsim_set2(v.simulation2_score);

                  await delay(200);    
                  v.myChart_sim.data.datasets[1].data.length = 0;

                  // v.chartsim_set(r.simulation_score);
                  for(let i=0;i<600;i++) {
                      let res = r.simulation_times[i];
                      let avg_speed = v.get_avg_speed(res);
                      v.chartsim_add2(i, avg_speed);
                      v.simulation2.push(res);
                   }
                   v.isLoading_screen = false;
                   v.stop_download("2nd Simulation Imported", 0);
              };

              reader.readAsText(file);    
          } else {
              alert("File not supported, .txt or .json files only");
          }
      });
      fileInput.click();
   },

   async simulation_import() {
     let v = this;
     var element = document.createElement('div');
     element.innerHTML = '<input type="file" accept=".json">';
     var fileInput = element.firstChild;

     fileInput.addEventListener('change', function() {
          var file = fileInput.files[0];

          if (file.name.match(/\.(xml|txt|json)$/)) {
              var reader = new FileReader();

              reader.onload = async function() {
                this.simulation_time = 0;
                this.isSumulation_loading = true;
                this.isSumulation_downloading = true;
                this.isSumulation_loading_time = Date.now();
                this.isSumulation_finish = false;
                
                  var r = JSON.parse(reader.result);

                   v.simulation_score = r.simulation_score;

                   v.traffic_load(r.sign_times, true , true);
                   v.simulation_open();
                   v.init_chartsim(true);
                   v.traffic_set(0);
                   v.simulation_init();
                   v.chartsim_set(r.simulation_score);

                  // v.isSimulation_text = "Please Wait. Importing simulation" ;
                  v.isLoading_screenText = "Please Wait. Importing simulation" ;
                  v.isLoading_screen = true;
                  await delay(200);    
                


                   for(let i=0;i<600;i++) {
                      let res = r.simulation_times[i];
                      // v.isSimulation_text = "Import simulations: " + (i+1) + "/600";
                      v.simulation_markers.add_values(res);
                      let avg_speed = v.get_avg_speed(res);
                      v.chartsim_add(i,avg_speed);
                      v.simulation.push(res);
                      // await delay(10);
                   }
                   v.isLoading_screen = false;
                   v.stop_download("Simulation Imported", 0);
              };

              reader.readAsText(file);    
          } else {
              alert("File not supported, .txt or .json files only");
          }
      });
      fileInput.click();
   },

   async simulation_download() {
      this.isSumulation_loading = true;
      let settings = JSON.stringify({
        "settings": JSON.stringify(this.sign_times)
      });

      this.simulation_time = 0;
      this.isSumulation_loading = true;
      this.isSumulation_downloading = true;
      this.isSumulation_loading_time = Date.now();
      this.isSumulation_finish = false;

      this.init_chartsim(true);
      let v = this;

      try {
        let v = this;
        var xmlhttp = new XMLHttpRequest();   // new HttpRequest instance 
        xmlhttp.timeout = 100000;
        xmlhttp.onreadystatechange = function() {
          if(this.readyState == 4) {
             if ((this.status == 200 || this.status == 201)) {
                  var status = JSON.parse(this.response);
                  v.isSumulation_downloading = false;
                  v.simulation_score = status.score;
                  v.simulation_download_times();
              } else {
               v.$notify({
                    title: 'Errors during calculation',
                    message:  this.status + ":" + this.statusText,
                    type: 'danger'
                  });
                v.stop_download("Error during calculation", 0, "error");
                return;
              }

          }
    
        };  
        xmlhttp.open("POST", url_compute);
        xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xmlhttp.send(settings)
        this.simulation_loop_status(xmlhttp);
        await delay(500);

      } catch(error) {
      this.$notify({
          title: 'Errors during calculation',
          message:  error,
          type: 'danger'
        });
        v.stop_download("Error during calculation", 0,  "error");
        return;
      }


    },



    simulation_close() {
      this.traffic_set(-1); // <-close all simulation
      this.isSimulation_open = false;
    },

    loading_close() {
      this.isLoading_open = false
    },

     async genetic_calculate() {
      this.isSimulation_open = false;
      this.isLoading_finish = false
      this.isLoading_text = 'Running'
      this.isLoading = true
      
      this.isLoading_open = true
      this.isShowGenetic = false
      this.panelType = ''
 
      this.map.panTo(
        [52.2176205
        ,20.9740354
        ])
      await delay(500)
      this.init_chart()
      // this.chart_add(0,this.time)


      let v = this
      let result = []
      try {
          await genetic.run(function(
            isFinished, 
            result,
            fitness, 
            pop, generation, stats) { 
            if(isFinished) {
                v.isLoading_finish = true
                v.isLoading_text = "Finished calculation"
                v.traffic_load(result, true, false)
            }
          },v.genetic_config, async function(i,max,best) {
            v.chart_add(i, best.fitness)
            v.traffic_show(best.entity, i%6==0)
            v.isLoading_text = "Running: " + i + "/" + max 
            v.isLoading_score = String(Math.round(best.fitness)) + " s"
            await delay(1)
          })
      }  catch (error) {
        }
     },

    showSimulation() {
       this.panelType =  this.panelType == 'sim' ? '' : 'sim'
     },

     simulation_load() {
       this.simulation_time = 0
     },

  

    simulation_change(el) {
      if(this.simulation_markers!=undefined) {

         this.simulation_markers.car_set(el);
         this.traffic_set(el);
      }
    },



    async simulation_play()  {
      this.isSimulation_play = true;
      const delay = ms => new Promise(res => setTimeout(res, ms));
       if(this.simulation_markers!=undefined)
        {
          for(let i = this.simulation_time;i< this.simulation_markers.size();i++){
            if(!this.isSimulation_play) return;

            this.simulation_time = i;
            this.simulation_markers.car_set(i);
            this.traffic_set(i);
            await delay(200);
          }

          
        }
        this.isSimulation_play = false;
        return;
     },

     simulation_pause() {
       this.isSimulation_play = false;
     },

    /** !Deprecated */
    playCars() {
      this.map.removeLayer(this.car);

      var myMovingMarker = L.Marker.movingMarker(
        [[52.221, 20.9853],[52.2163, 20.9811]],
        [5000], {  icon: this.car_icon,   rotationAngle: -58 }).addTo(this.map);
        var v = this
        myMovingMarker.addEventListener('end', function(e){
          v.map.removeLayer(myMovingMarker);
          v.uploadCars()
        });
      myMovingMarker.start()
    },


}
}
</script>

