<template>
  <div 
  
  @keyup.enter="$emit('run')"
  tabindex="-1" role="dialog" aria-modal="true" aria-label="Tip" class="el-message-box__wrapper" 
  style="z-index: 2001;">
    <div class="el-message-box" style="z-index: 3001;">
      <div class="el-message-box__header">
        <div class="el-message-box__title">
          <!---->
          <span>{{title}}</span>
        </div>
        <button type="button" aria-label="Close" class="el-message-box__headerbtn"   @click="$emit('cancel')">
          <i class="el-message-box__close el-icon-close"></i>
        </button>
      </div>
      <div class="el-message-box__content">
        <!---->
       <slot/>
      </div>
      <div class="el-message-box__btns">
        <button type="button" class="el-button el-button--default el-button--small" @click="$emit('cancel')">
          <!---->
          <!---->
          <span>
            Cancel
          </span>
        </button>
        <button type="button" class="el-button el-button--default el-button--small el-button--primary " @click="$emit('run')">
          <!---->
          <!---->
          <span>
            {{ok}}
          </span>
        </button>
      </div>
    </div>

    <div class="v-modal" tabindex="0" style="z-index: -2000;"></div>
  </div>
</template>
<script>
export default {
  beforeMount() {
    window.addEventListener('keyup', this.onEscapeKeyUp);
  },
  beforeDestroy () {
    window.removeEventListener('keyup', this.onEscapeKeyUp)
  },

  props: {
    'title': String,
    'ok': {
      type: String,
      default: 'OK'
    },
  },

 methods: {
    onEscapeKeyUp (event) {
      if (event.which === 27) {
        this.$emit('cancel');
      }
    },
  },
}
</script>

<style lang="scss" >
.el-message-box__wrapper {
.el-form {
  border: 0;
  padding: 0;
}

}
</style>
